<template>
	<div>
		<HeaderComponent :view-title="myActivityTitle"></HeaderComponent>
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	export default {
		myActivityTitle:Object,
		data(){
			return{
				myActivityTitle:{
					name:'我的活动',
					showCode:1
				}
			}
		},
		mounted(){},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped>
	
</style>


